@keyframes pulse {
  50% {
    opacity: 0.5;
  }
}
@keyframes fd-sidebar-in {
  from {
    transform: translateX(var(--fd-sidebar-mobile-offset));
  }
}
@keyframes fd-sidebar-out {
  to {
    transform: translateX(var(--fd-sidebar-mobile-offset));
  }
}
@keyframes fd-collapsible-down {
  from {
    height: 0;
    opacity: 0;
  }
  to {
    height: var(--radix-collapsible-content-height);
  }
}
@keyframes fd-collapsible-up {
  from {
    height: var(--radix-collapsible-content-height);
  }
  to {
    height: 0;
    opacity: 0;
  }
}
@keyframes fd-accordion-down {
  from {
    height: 0;
    opacity: 0.5;
  }
  to {
    height: var(--radix-accordion-content-height);
  }
}
@keyframes fd-accordion-up {
  from {
    height: var(--radix-accordion-content-height);
  }
  to {
    height: 0;
    opacity: 0.5;
  }
}
@keyframes fd-dialog-in {
  from {
    transform: scale(1.06);
    opacity: 0;
  }
  to {
    transform: scale(1);
  }
}
@keyframes fd-dialog-out {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.04);
    opacity: 0;
  }
}
@keyframes fd-popover-in {
  from {
    opacity: 0;
    transform: scale(0.7);
  }
}
@keyframes fd-popover-out {
  to {
    opacity: 0;
    transform: scale(0.7);
  }
}
@keyframes fd-fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fd-fade-out {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@keyframes fd-enterFromRight {
  from {
    opacity: 0;
    transform: translateX(200px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes fd-enterFromLeft {
  from {
    opacity: 0;
    transform: translateX(-200px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes fd-exitToRight {
  from {
    opacity: 1;
    transform: translateX(0);
  }
  to {
    opacity: 0;
    transform: translateX(200px);
  }
}
@keyframes fd-exitToLeft {
  from {
    opacity: 1;
    transform: translateX(0);
  }
  to {
    opacity: 0;
    transform: translateX(-200px);
  }
}
@keyframes fd-nav-menu-in {
  from {
    opacity: 0;
    height: 0px;
  }
  to {
    opacity: 1;
    height: var(--radix-navigation-menu-viewport-height);
  }
}
@keyframes fd-nav-menu-out {
  from {
    opacity: 1;
    height: var(--radix-navigation-menu-viewport-height);
  }
  to {
    opacity: 0;
    height: 0px;
  }
}
